
import { defineComponent } from "vue";
import { usePocketbaseStore } from "@/stores/pocketbase";

export default defineComponent({
  name: "SignIn",
  data: () => ({
    form: false,
    loading: false,
    authKey: "" as string,
    password: "" as string,
    showPassword: false,
    errors: [] as string[],
  }),
  setup() {
    const store = usePocketbaseStore();
    return { store };
  },
  methods: {
    onSubmit() {
      if (!this.form) return;
      this.errors = [];
      this.loading = true;
      this.signIn();
    },
    async signIn() {
      if (!this.store.getPb) {
        console.error("Can't get Pocketbase instance from storage");
        return;
      }

      try {
        await this.store.getPb
          .collection("users")
          .authWithPassword(this.authKey, this.password);
      } catch (error) {
        this.errors.push("User with these credentials does not exist");
        this.loading = false;
        return;
      }

      this.loading = false;
      this.$router.push("profile");
    },
    required(v: string) {
      return !!v || "Field is required";
    },
    passwordLength(v: string) {
      return (
        (v.length >= 8 && v.length <= 72) ||
        "The length must be between 8 and 72"
      );
    },
  },
});
